import './MyHeader.css'
import { Row, Col, Icon } from 'antd'


export default () => (
    <Row type="flex" justify="space-between" align='middle' className='header'>
        <img src='../static/me.jpg' className='avatar' />
        <Col span={16} className="heading">
             <Row>Jaime Salazar Lahera</Row>
             <Row>
                 <span className='master'>Master </span> 
                 of
                 <span className='engineering'> Engineering</span> 
             </Row>
        </Col>
        <Col span={8} className="contact">
            <Row>Email<Icon type='mail' className='logo' /></Row>
            <Row>Connect<a href='https://linkedin.com/in/jaime-salazar-lahera' target='_blank'><Icon type='linkedin' className='logo' /></a></Row>
            <Row>Checkout<a href='https://github.com/jsalaz1989' target='_blank'><Icon type='github' className='logo' /></a></Row>
        </Col>
    </Row>
)